/*------------------------------------------------------------
	Slider
------------------------------------------------------------*/
.swiper {
  width: 100vw;
  height: 100vh;
}
.swiper .overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: rgba(0, 0, 0, 0.6);
  margin: 0;
}
@media (min-width: 640px) {
  .swiper .overlay {
    background: rgba(0, 0, 0, 0.3);
  }
}
.swiper .swiper-image {
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin: 0;
  transform: scale3d(1, 1, 1);
  -webkit-backface-visibility: hidden !important;
          backface-visibility: hidden !important;
  will-change: transform;
  transition: transform 1500ms ease;
  transition-delay: 0.4s;
}
.swiper .content-wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: ".";
  height: 75%;
  width: 100%;
  justify-items: start;
  align-items: end;
}
@media (min-width: 640px) {
  .swiper .content-wrapper.content-right {
    justify-items: end;
  }
}
.swiper .content-wrapper .content {
  padding: 1.5em;
  color: #fff;
  opacity: 0;
  width: 100%;
  max-width: 300px;
  transition-duration: 1.5s, 0.6s;
  transition-property: opacity, transform;
  transition-delay: 0.4s;
}
.swiper .content-wrapper .content :first-child {
  margin: 0;
}
@media (min-width: 640px) {
  .swiper .content-wrapper .content {
    padding: 0 5%;
  }
}
.swiper .swiper-slide {
  margin: 0;
  overflow: hidden !important;
}
.swiper .swiper-slide.swiper-slide-active .swiper-image {
  transform: scale3d(1.02, 1.02, 1);
  will-change: transform;
}
.swiper .swiper-slide.swiper-slide-active .content {
  opacity: 1;
}
.swiper .swiper-nav-wrapper {
  position: absolute;
  bottom: 6em;
  right: 6em;
}
@media (min-width: 640px) {
  .swiper .swiper-nav-wrapper {
    bottom: 4.5em;
  }
}
.swiper .swiper-nav-wrapper .swiper-button-next {
  right: 0;
  left: 0;
}
.swiper .swiper-nav-wrapper .swiper-button-prev {
  right: 15px;
  left: auto;
}
.swiper .swiper-nav-wrapper .swiper-button-next,
.swiper .swiper-nav-wrapper .swiper-button-prev {
  background: rgba(0, 0, 0, 0.5);
  width: 60px;
  height: 60px;
}
.swiper .swiper-nav-wrapper .swiper-button-next:hover,
.swiper .swiper-nav-wrapper .swiper-button-prev:hover {
  background: rgba(0, 0, 0, 0.8);
}

.swiper-button-next:after,
.swiper-button-prev:after {
  display: none;
}

